<template>
	<view class="">
		<view class="">
			<view class="username">
				请输入用户名: <input style="padding-top: 20rpx;" v-model="username" type="text" value="" />
			</view>
			<view class="password">
				请输入密码： <input style="padding-top: 20rpx;" v-model="password" type="text" value="" />
			</view>
			<view class="login">
				<button type="default" @click="check()">确认</button>
			</view>
		</view>
	</view>
</template>

<script>
	import axios from 'axios'
	import Qs from 'qs'
	export default {
		data() {
			return {
				username: '',
				password: ''
			}
		},
		methods: {
			check() {
				uni.request({
					url: 'http://localhost:8001/register',
					method: 'POST',
					data: Qs.stringify({
						username: this.username,
						password: this.password
					}),
					success: (res) => {
						uni.showToast({
							title: res.data.message,
							duration: 1000
						});
						setTimeout(() => {
							uni.navigateTo({
								url: './login'
							});
						}, 1100)
					},
					header: {
						'Content-Type': 'application/x-www-form-urlencoded',
					},
					fail: () => {
						console.log('失败')
					}
				})
			}
		}
	}
</script>

<style scoped lang="scss">
	.username {
		width: 600rpx;
		margin: 40rpx auto;
		border-bottom: 2rpx solid #000000;
	}

	.password {
		width: 600rpx;
		margin: 0rpx auto;
		border-bottom: 2rpx solid #000000;
	}

	.login {
		button {
			width: 200rpx;
			height: 80rpx;
			margin-top: 20rpx;
			color: red;
			background-color: white;
			border: 2rpx solid red;
			border-radius: 20rpx;
			text-align: center;
		}

	}
</style>
